.landing-page {
  display: flex;
  flex-direction: column;
  justify-content: center;

  height: 90vh;
  width: 100vw;
}

.landing-message {
  align-self: center;
  width: 500px;
  height: 364px;
  left: 573px;
  top: 300px;
  font-style: normal;
  font-weight: 600;
  font-size: 20px;
  line-height: 23px;
}

.landing-header {
  display: flex;
  flex-direction: row;
  justify-content: center;
  text-align: center;
}

#landing-app-logo {
  width: 140px;
  height: 125px;
  filter: drop-shadow(3px 3px 3px rgba(0, 0, 0, 0.137));
}
h2 {
  text-align: center;
  align-self: center;
  font-style: normal;

  font-size: 72px;
  text-shadow: 4px 4px 4px rgba(0, 0, 0, 0.07);
  line-height: 23px;
  text-align: center;
  color: #3ebbbf;
  font-weight: 600;
  margin-bottom: 65px;
  font-family: "Poppins", sans-serif;
}

.tag-line {
  display: flex;
  justify-content: center;
  flex-direction: column;
}

h3 {
  font-family: "Nunito Sans", sans-serif;
  font-weight: 600;
  font-size: 22px;
  line-height: 23px;
  text-align: center;
  margin-top: 20px;
  margin-bottom: 50px;
  margin-right: 10px;
  margin-left: 10px;
  color: rgba(0, 0, 0, 0.541);
  text-shadow: 4px 4px 4px rgba(0, 0, 0, 0.07);
}
.landing-links {
  display: flex;
  justify-content: center;
  align-items: row;
}
.signup-link-landing {
  font-family: "Poppins", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 23px;
  line-height: 23px;
  color: rgba(0, 0, 0, 0.541);
  text-shadow: 4px 4px 4px rgba(0, 0, 0, 0.1);
  margin-right: 20px;
}
.login-link-landing {
  font-family: "Poppins", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 23px;
  line-height: 23px;
  color: rgba(0, 0, 0, 0.541);
  text-shadow: 4px 4px 4px rgba(0, 0, 0, 0.1);
  text-align: center;
  margin-left: 20px;
}
